<template src="@/assets/dataBlock/dataBlock.html"></template>

<script>
// @ is an alias to /src
import { db } from '@/db'
const fStore = db.firestore()

export default {
  name: 'complete',
  data() {
    return {
      todoList: [],
    }
  },
  created() {
    this.$bind(
      'todoList',
      fStore.collection('todo-List').where('complete', '==', true)
    )
  },
  methods:{
    //刪除資料
    deleteData(event){
      let key = event.target.dataset.name;
      fStore.collection("todo-List").doc(key).delete().then(function() {
          console.log("資料成功刪除");
      }).catch(function(error) {
          console.error("資料刪除失敗", error);
      });
    },
    //更新資料
    updateData(event){
      let vm = this;
      let key = event.target.dataset.name;
      let numKey = event.target.dataset.num;
      fStore.collection("todo-List")
        .doc(key)
        .update({
          'commentText': vm.todoList[numKey].commentText,
          'complete':vm.todoList[numKey].complete,
          'date':vm.todoList[numKey].date,
          'star':vm.todoList[numKey].star,
          'time':vm.todoList[numKey].time,
          'title':vm.todoList[numKey].title,
        })
        .then(() => console.log('資料更新成功'));
        vm.todoList[numKey].open = false;
    },
  },
}
</script>

<style scoped lang="scss" src="@/assets/dataBlock/dataBlock.scss"></style>